        .scaleContainer {
          p {
          }
        }
      }
    }
  }

  .skillsBarContainer1 {
    .skillsBarContainer2 {
      h4 {
      }
    }
  }

  .progress {
    .progress-bar {
      span {
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .backgroundContainer {
    .backgroundArticleLeft {
      padding: 10px;
      width: 90%;
      box-sizing: border-box;

      #skillsHeading {
        font-size: 1.1rem;
      }
      .scaleOptionsContainer {
        padding-left: 23%;

        .scaleContainer {
          width: 100%;
          justify-content: space;

          p {
            font-size: 0.8rem;
            width: 33%;
          }
        }
      }
    }
  }

  .skillsBarContainer1 {
    .skillsBarContainer2 {
      justify-content: space-evenly;
      width: 100%;

      h4 {
        font-size: 0.8rem;
      }
    }
  }

  .progress {
    width: 70%;

    .progress-bar {
      span {
      }
    }
  }
}
